
import { Options, Vue } from "vue-class-component";
import { Logger } from "@/lib/Logger";
import { Plotter } from "@/lib/Plotter";
import { GCodeParser } from "@/lib/GCodeParser";

@Options({
  props: {
    plotter: Plotter,
  },
})
export default class GCodeUpload extends Vue {
  plotter!: Plotter;

  file: File[] = [];

  async send(): Promise<void> {
    Logger.info(this.file);
    const parser = new GCodeParser(this.file[0]);
    await parser.parse();
    await this.plotter.sendGCode(parser);
  }
}
