
import { Options, Vue } from "vue-class-component";
import { Plotter, PlotterSteppers } from "@/lib/Plotter";

@Options({
  props: {
    plotter: Plotter,
  },
})
export default class StepperMove extends Vue {
  plotter!: Plotter;

  stepperOptions = Object.keys(PlotterSteppers)
    .filter((value) => !isNaN(Number(value)))
    .map((key) => {
      const obj = PlotterSteppers[key as keyof typeof PlotterSteppers];
      return {
        text: obj.toString(),
        value: parseInt(key),
      };
    });
  stepper = this.stepperOptions[0];

  steps = 0;
  velocity = 0;

  async sendStepper(): Promise<void> {
    this.plotter.sendStepper(this.stepper.value, this.steps, this.velocity);
  }
}
