import { Vue } from "vue-class-component";
import { reactive } from "vue";
import log from "loglevel";
import logprefix from "loglevel-plugin-prefix";

export class Logger extends Vue {
  public static logs: Array<string> = reactive([]);

  public static init(): void {
    logprefix.reg(log);
    log.enableAll();
    logprefix.apply(log, {
      format(level, name, timestamp) {
        return `${timestamp} ${level} ${name}:`;
      },
    });

    this.clear();
  }

  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  private static addLine(...msg: any[]) {
    const str = msg.toString();
    if (str.length > 0) this.logs.push(str);
  }

  public static clear(): void {
    let val: string | undefined;
    do {
      val = this.logs.pop();
    } while (val !== undefined);
  }

  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  public static log(...msg: any[]): void {
    log.log(msg);
    this.addLine(msg);
  }

  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  public static info(...msg: any[]): void {
    log.info(msg);
    this.addLine(msg);
  }

  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  public static warn(...msg: any[]): void {
    log.warn(msg);
    this.addLine(msg);
  }

  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  public static error(...msg: any[]): void {
    log.error(msg);
    this.addLine(msg);
  }
}
