
import { Options, Vue } from "vue-class-component";
import { Port } from "@/lib/Port";
import { Plotter } from "@/lib/Plotter";
import { Logger } from "@/lib/Logger";

@Options({
  props: {
    plotter: Plotter,
  },
})
export default class PortsList extends Vue {
  // eslint-disable-next-line no-undef
  ports: SerialPort[] | null = null;
  plotter: Plotter | null = null;

  mounted(): void {
    this.getPorts();
  }

  async getPorts(): Promise<void> {
    Logger.info("Getting Ports");
    this.ports = await Port.getPorts();
  }
}
