
import { Options, Vue } from "vue-class-component";
import PortsList from "@/components/PortsList.vue";
import HelloWorld from "@/components/HelloWorld.vue"; // @ is an alias to /src
import LoggerDisplay from "@/components/LoggerDisplay.vue";
import ManualMove from "@/components/ManualMove.vue";
import StepperMove from "@/components/StepperMove.vue";
import GCodeUpload from "@/components/GCodeUpload.vue";
import { Logger } from "@/lib/Logger";
import { Plotter } from "@/lib/Plotter";

@Options({
  components: {
    HelloWorld,
    PortsList,
    LoggerDisplay,
    ManualMove,
    StepperMove,
    GCodeUpload,
  },
})
export default class Home extends Vue {
  plotter: Plotter | null = null;

  get connected(): boolean {
    return this.plotter !== null;
  }

  setup(): void {
    const webAPISerialSupport = "serial" in navigator;

    if (!webAPISerialSupport) {
      Logger.error("The Web Serial API is not supported.");
    }
  }

  async connectPort(): Promise<void> {
    if (this.plotter !== null) throw new Error("Port already connected");
    this.plotter = new Plotter();
    this.plotter.connect();
  }
}
