import { Logger } from "@/lib/Logger";
export class Port {
  static readonly TIMEOUT_INTERVAL = 100;
  private static readonly POLL_ACK_INTERVAL = 100; //ms
  private static readonly POLL_ACK_TIMEOUT = 5000; //ms

  ack = true;
  port: SerialPort | null = null;
  receiveCallback: ((data: Uint8Array) => Promise<void>) | null = null;

  timeoutIntervalId = -1;
  currentPacket: Array<number> = [];

  public static async getPorts(): Promise<SerialPort[]> {
    const ports = await navigator.serial.getPorts();
    return ports;
  }

  public async open(): Promise<void> {
    if (this.receiveCallback === null)
      throw new Error("Receive Callback has not been set");
    try {
      const port = await navigator.serial.requestPort();
      Logger.info("Opening port with 9600 baud");
      await port.open({ baudRate: 9600 });
      this.port = port;
    } catch (error) {
      Logger.error(error);
    }
  }

  public async write(data: Uint8Array): Promise<void> {
    if (this.port === null) throw new Error("Write called on Null Port");
    if (this.port.writable === null) throw new Error("Writer is not Writable");

    const packet = new Uint8Array(data.byteLength + 1);
    packet.set(data);
    packet.set(new Uint8Array([0]), data.byteLength);

    const writer = this.port.writable.getWriter();

    this.ack = false;

    Logger.info(packet);
    await writer.write(packet);
    writer.releaseLock();
  }

  public async readPort(): Promise<void> {
    if (this.port === null) throw new Error("Read called on Null Port");
    Logger.info("Starting Read Loop");
    while (this.port.readable) {
      const reader = this.port.readable.getReader();
      try {
        const reading = true;
        while (reading) {
          const { value, done } = await reader.read();
          if (done) break;
          if (value !== undefined) {
            this.addToPacket(value);
          }
        }
        // eslint-disable-next-line @typescript-eslint/no-explicit-any
      } catch (error: any) {
        Logger.error(error.toString());
      } finally {
        reader.releaseLock();
      }
    }
  }

  public async close(): Promise<void> {
    await this.port?.close();
  }

  private addToPacket(data: Uint8Array): void {
    clearTimeout(this.timeoutIntervalId);
    for (let offset = 0; offset < data.length; offset++) {
      this.currentPacket.push(data[offset]);
    }
    this.timeoutIntervalId = setTimeout(
      this.emitPacket.bind(this),
      Port.TIMEOUT_INTERVAL
    );
  }

  private async emitPacket(): Promise<void> {
    clearTimeout(this.timeoutIntervalId);
    if (this.currentPacket.length > 0) {
      await this.receiveCallback?.(new Uint8Array(this.currentPacket));
      this.ack = true;
    }
    this.currentPacket = [];
  }

  public async ackOrTimeout(timeoutMs?: number): Promise<boolean> {
    return new Promise((r) => {
      let timeoutms =
        timeoutMs !== undefined ? timeoutMs : Port.POLL_ACK_TIMEOUT;
      const check = () => {
        if (this.ack) r(true);
        // Check if timeout reached
        else if ((timeoutms -= Port.POLL_ACK_INTERVAL) < 0) r(false);
        // Keep waiting
        else setTimeout(check, Port.POLL_ACK_INTERVAL);
      };
      // Start Poll
      setTimeout(check, Port.POLL_ACK_INTERVAL);
    });
  }
}
