
import { Options, Vue } from "vue-class-component";
import { Plotter, PlotterDirections } from "@/lib/Plotter";

@Options({
  props: {
    plotter: Plotter,
  },
})
export default class LoggerDisplay extends Vue {
  plotter!: Plotter;

  PlotterDirections = PlotterDirections;

  x = 0.0;
  y = 0.0;

  async sendXY(): Promise<void> {
    await this.plotter.sendXY(this.x, this.y);
  }

  async sendMove(dir: number): Promise<void> {
    await this.plotter.sendMove(dir);
  }
}
